const roles = {
  0: {
    name: '',
    icon: 'star',
  },
  1: {
    name: '',
    icon: 'tag',
  },
  2: {
    name: '',
    icon: 'user',
  },
};

export default {
  props: {
    value: {
      type: Number,
      default: 2,
      validator: (v) =>
        v >= 0 && v <= 2,
    },
  },

  computed: {
    role() {
      return roles[this.value];
    },
  },
};
